import { useCallback, ChangeEvent } from "react";

import KeyboardIcon from "@mui/icons-material/Keyboard";
import Box from "@mui/material/Box";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import CardMedia from "@mui/material/CardMedia";
import FormControl from "@mui/material/FormControl";
import InputAdornment from "@mui/material/InputAdornment";
import InputLabel from "@mui/material/InputLabel";
import OutlinedInput from "@mui/material/OutlinedInput";
import TextField from "@mui/material/TextField";
import Typography from "@mui/material/Typography";
import { styled } from "@mui/material/styles";

const styles = {
  itemDescription: {
    display: "-webkit-box",
    WebkitLineClamp: "4",
    WebkitBoxOrient: "vertical",
    textOverflow: "ellipsis",
    overflow: "hidden",
  },
};

const ResponsiveBox = styled(Box)(({ theme }) => ({
  display: "flex",
  margin: theme.spacing(2),
  [theme.breakpoints.down("sm")]: {
    flexDirection: "column-reverse",
    alignItems: "center",
  },
}));

interface ItemSummaryProps {
  imageUrl: string;
  itemNbr: number;
  itemUPC?: string;
  itemDescription?: string;
  value: string;
  setValue: (value: string) => void;
  inputLabel?: string;
  isUpdate?: boolean;
  remainingQty?: number;
  isReReceivingContainer?: boolean | null
}
export const ItemSummary = ({
  imageUrl,
  itemNbr,
  itemUPC,
  itemDescription,
  value,
  setValue,
  inputLabel,
  isUpdate,
  remainingQty,
  isReReceivingContainer
}: ItemSummaryProps) => {
  const onChangeQty = useCallback(
    (event: ChangeEvent<HTMLInputElement>) => {
      const val = event.target.value;
      // accpet only integers >= 0
      const matched = /^\d*$/.test(val);
      if (matched) {
        setValue(val);
      }
    },
    [setValue]
  );

  const checkQuantity = () => {
    const quantity = remainingQty || 0;
    return quantity > Number(value);
  };

  const isQuantitySame = () => {
    const quantity = remainingQty || 0;
    return quantity === Number(value);
  };

  return (
    <ResponsiveBox>
      <Card sx={{ width: ["100%", "30ch"] }}>
        <CardMedia
          component="img"
          image={imageUrl}
          alt={`${itemDescription}`}
          sx={{ height: [240] }}
        />
        <CardContent>
          <Typography gutterBottom variant="h6" component="div">
            {`UPC: ${itemUPC}`}
          </Typography>
          <Typography gutterBottom variant="h6" component="div">
            {`Item: ${itemNbr}`}
          </Typography>
          <Typography
            variant="body2"
            color="text.secondary"
            sx={styles.itemDescription}
          >
            {itemDescription}
          </Typography>
        </CardContent>
      </Card>
      {isUpdate && (
        <>
          <FormControl
            sx={{ m: 4, mt: 0, width: ["100%", "15ch"] }}
            variant="outlined"
          >
            <p>
              <b>Remaining Qty</b>
            </p>
            <p>
              <b>{remainingQty}</b>
            </p>
          </FormControl>
          <FormControl
            sx={{ m: 2, mt: 0, width: ["100%", "40ch"] }}
            variant="outlined"
          >
            <p>
              <b>{inputLabel}</b>
            </p>
            <TextField
              id={inputLabel}
              disabled={isReReceivingContainer == true} 
              error={!checkQuantity() || (isReReceivingContainer == true)}
              helperText={
                isReReceivingContainer ? (
                  `Quantity Correction cannot be done for 2-Tier Re-Receiving Container`
                ) : (
                  !checkQuantity()
                  ? !isQuantitySame()
                    ? `Enter quantity less than ${remainingQty}`
                    : `The quantity entered will result in no change, enter a quantity less than  ${remainingQty} to adjust the label`
                  : ""
                )
              }
              type="number"
              autoComplete="off"
              value={value}
              onChange={onChangeQty}
            />
          </FormControl>
        </>
      )}
      {!isUpdate && (
        <>
          <FormControl
            sx={{ m: 2, mt: 0, width: ["100%", "40ch"] }}
            variant="outlined"
          >
            <OutlinedInput
              id={inputLabel}
              type="number"
              autoComplete="off"
              value={value}
              onChange={onChangeQty}
              endAdornment={
                <InputAdornment position="end">
                  <KeyboardIcon />
                </InputAdornment>
              }
              label={inputLabel}
            />
            <InputLabel>{inputLabel}</InputLabel>
          </FormControl>
        </>
      )}
    </ResponsiveBox>
  );
};
