import { useMutation } from "@tanstack/react-query";
import { AxiosError, AxiosResponse } from "axios";
import { useCallback, useState, useContext, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { v4 as uuid } from "uuid";

import { Chip } from "@mui/material";
import Button from "@mui/material/Button";

import { AlertContext } from "../../contexts/alert";
import { GlobalsContext } from "../../contexts/globals";
import {
  DeliveryDocument,
  InstructionResponse,
  PrintJob,
} from "../../types/instruction";
import { MyAppsData } from "../../types/my-apps";
import { PrintRequestResponse } from "../../types/printing";
import { apiHandler } from "../../utilities/api-handler";
import { GetReceiveInstructionRequest } from "../../utilities/api-handler";
import { appLogger } from "../../utilities/app-logs/app-logger";
import { axiosRequest } from "../../utilities/axios-request";
import { printingHandler } from "../../utilities/printing/printing-handler";
import { useLazyPrint } from "../../utilities/printing/useLazyPrint";
import {
  getItemDescription,
  getUpc,
} from "../../utilities/regulated-item-methods";
import { sessionHandler } from "../../utilities/session-handler";
import { useHandlePageRefresh } from "../../utilities/useHandlePageRefresh";
import { CustomDialogBox } from "../common/dialog-box/dialog-box";
import { SubHeader } from "../common/sub-header/sub-header";
import { useGlobalKeyboard } from "../global-keyboard/useGlobalKeyboard";
import { isRegulatedItem } from "../regulated-item/isRegulatedItem";
import { RegulatedItemModal } from "../regulated-item/regulated-item-modal";
import { ScanUpcNavState } from "../scan-upc/scan-upc";
import { styles } from "./item-quantity.styles";
import { ItemSummary } from "./item-summary";
import DialogHeavyLift from "../common/dialog-box/dialog-heavy-lift";
import { HeavyLiftLabel } from "../../types/heavyLiftLablel";
import DialogOverage from "../common/dialog-box/dialog-overage";

const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
const enableQtyCorrection =
  myAppsData?.dynamicConfig.enableQtyCorrection || false;
const enableNewTCLRoute = myAppsData?.dynamicConfig.enableNewTCLRoute || false;
const enableHeavyLiftLabel = myAppsData?.dynamicConfig.enableHeavyLiftLabel || false;
const heavyLiftThreshold = myAppsData?.dynamicConfig.heavyLiftThreshold || 1e9;
const heavyLiftCorrelationId = uuid();

const postItemReceiveRequest = (
  receiveInstructionRequest: GetReceiveInstructionRequest
) =>
  axiosRequest(apiHandler.getReceiveInstruction(receiveInstructionRequest), {
    description: `Item-Quantity: receive items for upc ${receiveInstructionRequest.upcNumber} delivery ${receiveInstructionRequest.deliveryNumber}`,
  });

export interface ItemQuantityNavState extends ScanUpcNavState {
  trackingId: string;
  printJob?: PrintJob;
}

/**
 * @route Receives user entered quantity
 *
 * @discription
 * diaplays alerts -> hazmat, lithium, limited.
 *
 * if receive instruction's inventoryStatus === PICKED => its a casepack, print the label
 *
 * if receive instruction's inventoryStatus === AVAILABLE => its a breakpack, route to Breakpack
 *
 * else for other inventory status, display error
 */
export default function ItemQuantity() {
  // get state values from scan-dock-tag page
  const scanUpcNavState = useHandlePageRefresh<ScanUpcNavState>();
  const navigate = useNavigate();
  const { alerts, setAlerts } = useContext(AlertContext);
  const { globals, setGlobals } = useContext(GlobalsContext);
  const [qty, setQty] = useState("");
  const [openRegulatedModal, setopenRegulatedModal] = useState(false);
  const [openOverageAlert, setOpenOvergaeAlert] = useState(false);
  const [trustedSellerWarning, setTrustedSellerWarning] = useState(false);
  const [trustedTag, setTrustedTag] = useState(false);
  const [header, setHeader] = useState(<></>);
  const [content, setContent] = useState(<></>);
  const [chipContent, setChipContent] = useState(<></>);
  const [receivingLargeQuantityDlg, setReceivingLargeQuantityDlg] =
    useState(false);
  const [localStoragePrevScanDataList, setLocalStoragePrevScanDataList] =
    useState<string[]>();
  const [showHeavyLiftPopUp, setShowHeavyLiftPopUp] = useState<Boolean>(false);
  const [printJobData, setPrintJobData] = useState<PrintJob | null>(null);
  const [trackingId, setTrackingId] = useState<String | undefined>();
  const [containerWeight, setContainerWeight] = useState<number | null>(null);
  const [showHardOveragePopUp, setShowHardOveragePopUp] = useState<boolean>(false);
  var correlationId: string;
  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const enableRegulatedItemChange =
    myAppsData?.dynamicConfig.enableRegulatedItemChange || false;
  const enableReceivingLargeQuantityLimit =
    myAppsData?.dynamicConfig.enableReceivingLargeQuantityLimit || false;
  const RCV_SOFT_LIMIT = myAppsData?.dynamicConfig.receivingSoftLimit || 0;

  const printInstructionPrintJobSuccess = useCallback(
    (response: PrintRequestResponse[]) => {
      appLogger.info("Item-Quantity: printjob success", response);
      setAlerts({ success: "Labels were sent to printing" });
      appLogger.action("Item-Quantity: navigate to scan-dock-tag page");
      navigate("/scan-dock-tag-or-tcl", {
        state: {
          cartId: scanUpcNavState?.cartId,
          doorNumber: scanUpcNavState?.doorNumber,
        },
      });
    },
    [setAlerts, scanUpcNavState, navigate]
  );

  const getChipsDetails = useCallback(() => {
    if (globals.multiSku && trustedTag) {
      setChipContent(
        <>
          <Chip label={"MULTI SKU"} color="primary" sx={styles.chip} />
          <Chip label={"TRUSTED"} color="info" sx={styles.chip} />
        </>
      );
    } else if (globals.multiSku) {
      setChipContent(
        <>
          <Chip label={"MULTI SKU"} color="primary" sx={styles.chip} />
        </>
      );
    } else if (trustedTag) {
      setChipContent(
        <>
          <Chip label={"SINGLE SKU"} color="default" sx={styles.chip} />
          <Chip label={"TRUSTED"} color="success" sx={styles.chip} />
        </>
      );
    } else {
      setChipContent(
        <>
          <Chip label={"SINGLE SKU"} color="default" sx={styles.chip} />
        </>
      );
    }
  }, [globals.multiSku, trustedTag]);

  useEffect(() => {
    if (scanUpcNavState && scanUpcNavState.itemQuantity) {
      setQty(scanUpcNavState.itemQuantity);
      setTrustedTag(true);
    }
    if (globals.previouslyScannedDataList.length > 0) {
      localStorage.setItem(
        "localStoragePrevScanDataList",
        JSON.stringify(globals.previouslyScannedDataList)
      );
      setLocalStoragePrevScanDataList(globals.previouslyScannedDataList);
    } else {
      setLocalStoragePrevScanDataList(
        JSON.parse(localStorage.getItem("localStoragePrevScanDataList") + "")
      );
    }
    getChipsDetails();
  }, [
    scanUpcNavState,
    setQty,
    setTrustedTag,
    getChipsDetails,
    setLocalStoragePrevScanDataList,
  ]);

  const printInstructionPrintJobError = useCallback(
    (error: unknown) => {
      appLogger.error("Item-Quantity: printjob error", error as object);
      setAlerts({
        error:
          "There was an error printing. Check the printer status or re-connect to continue.",
      });
    },
    [setAlerts]
  );

  const [printInstructionPrintJob] = useLazyPrint({
    onPrintRequestSuccess: printInstructionPrintJobSuccess,
    onPrintRequestError: printInstructionPrintJobError,
  });

  const receiveItemsSuccess = (
    response: AxiosResponse<InstructionResponse>
  ) => {
    // resetting previouslyScannedDataList on successful recive instruction.
    setGlobals({
      ...globals,
      previouslyScannedDataList: [],
    });

    // check for OverageReceiving Instruction Code
    const instructionCode = response.data.instruction?.instructionCode || null;
    if(instructionCode == 'WFSOverageReceiving'){
      setShowHardOveragePopUp(true);
      setAlerts({ loading: false });
      return;
    }

    // before printing we need to identify casepack or breakpack
    // print labels on if casepack
    const inventoryStatus =
      response.data.instruction?.container.inventoryStatus;
    const { printJob } = response.data;
    
    setTrackingId(response.data.instruction?.container?.trackingId);

    switch (inventoryStatus) {
      case "PICKED": {
        // casepack
        if (printJob) {
          appLogger.info("Item-Quantity: printjob request", printJob);
          setPrintJobData(printJob);
          const itemWeight = parseFloat(response.data.deliveryDocuments[0].deliveryDocumentLines.filter(item => item.itemUpc === scanUpcNavState?.scannedUpc)[0]?.additionalInfo?.weightQty) || 0;
          const totalWeight = itemWeight*parseFloat(qty);
          setContainerWeight(totalWeight);

          if(enableHeavyLiftLabel && totalWeight >= heavyLiftThreshold){
            const logInfo: HeavyLiftLabel = {
              trackingId: response.data.instruction?.container?.trackingId,
              correlationId: heavyLiftCorrelationId
            };
            appLogger.info('Heavy Lift Label Popup: Popup Displayed', logInfo)
            setShowHeavyLiftPopUp(true);
          }
          else{
            printInstructionPrintJob(printJob);
          }
          
        } else {
          appLogger.error("Item-Quantity: printjob not found", {});
          console.error("No print job found");
          setAlerts({ error: "No print job found" });
        }
        break;
      }
      case "AVAILABLE": {
        // breakpack
        const trackingId =
          response.data.instruction?.container.trackingId || "";
        appLogger.action("Item-Quantity: navigate to breakpack page");
        if (!enableQtyCorrection) {
          setGlobals({ ...globals, disableQuantityCorrection: false });
        }
        navigate("/breakpack", {
          state: {
            ...scanUpcNavState,
            splitQty: 0,
            trackingId,
            printJob,
          },
        });
        break;
      }
      default: {
        appLogger.error(
          `Item-Quantity: inventoryStatus ${inventoryStatus} not supported`,
          {}
        );
        console.error("Other types are not supported yet");
        setAlerts({
          error: `Other inventoryStatus ${inventoryStatus} not supported.`,
        });
        break;
      }
    }
  };

  const receiveItemsError = (error: AxiosError<any>) => {
    setAlerts({
      error:
        error?.response?.data?.errorMessage ||
        error?.message ||
        "Error while receiving",
    });
  };

  const { mutate: receiveItems } = useMutation(postItemReceiveRequest, {
    onSuccess: (response) =>
      receiveItemsSuccess(response as AxiosResponse<InstructionResponse>),
    onError: (error) => receiveItemsError(error as AxiosError<any>),
  });

  const receive = useCallback(async () => {
    if (scanUpcNavState?.scannedUpc && scanUpcNavState?.doorNumber) {
      // backend needs gtin prop added to line level
      // which doesnt exists from gdm response
      const UpdatedDeliveryDocsWithGtin = addGtinToDocumentLine(
        scanUpcNavState?.gdmDeliveryDocuments
      );
      const receiveItemRequest = {
        deliveryNumber: `${scanUpcNavState?.deliveryNumber}`,
        doorNumber: `${scanUpcNavState?.doorNumber}`,
        upcNumber: scanUpcNavState?.scannedUpc,
        deliveryDocuments: UpdatedDeliveryDocsWithGtin,
        enteredQty: parseInt(qty),
        isMultiSKUItem: globals.multiSku,
        scannedDataList: scanUpcNavState.scannedDataList,
        receivingType: scanUpcNavState.receivingType,
        previouslyScannedDataList: localStoragePrevScanDataList,
      };
      const isprinterSelected = await printingHandler.isPrinterSelected();
      if (isprinterSelected) {
        // trigger loading
        setAlerts({ loading: true });
        if (enableRegulatedItemChange) {
          receiveItems(
            Object.assign(receiveItemRequest, {
              isVendorComplianceValidated: true,
            })
          );
        } else {
          receiveItems(receiveItemRequest);
        }
      } else {
        setAlerts({
          error: "No printer selected, please select a printer to receive",
        });
        appLogger.error(
          "Item-Quantity: No printer selected, please select a printer to receive",
          {}
        );
      }
    } else {
      setAlerts({
        error: "doorNumber or upcNumber is missing",
      });
      appLogger.error("Item-Quantity: doorNumber or upcNumber is missing", {});
    }
  }, [
    qty,
    scanUpcNavState,
    setAlerts,
    receiveItems,
    globals.multiSku,
    globals.previouslyScannedDataList,
    enableRegulatedItemChange,
  ]);

  const onClickCancel = useCallback(() => {
    appLogger.action("Item-Quantity: cancel button clicked");
    if (`${scanUpcNavState?.isTclFreeFlow}`) {
      navigate(-2);
    }
    navigate(-1);
  }, [navigate]);

  const onClickBack = useCallback(() => {
    appLogger.action("Item-Quantity: Back button clicked");
    if (`${scanUpcNavState?.isTclFreeFlow}`) {
      navigate(-2);
    }
    else{
      navigate(-1);
    }
  }, [navigate])

  const onClickReceive = useCallback(() => {
    appLogger.action("Item-Quantity: receive button clicked");
    // check if regulated item
    const isRegulated = !enableRegulatedItemChange
      ? isRegulatedItem(
          scanUpcNavState?.gdmDeliveryDocuments[0].deliveryDocumentLines[0]
            .transportationModes
        )
      : null;
    if (isRegulated) {
      // show regulated item popup
      appLogger.action("Item-Quantity: show regulated item pop up");
      setopenRegulatedModal(true);
    } else {
      receive();
    }
  }, [
    receive,
    setopenRegulatedModal,
    scanUpcNavState,
    enableRegulatedItemChange,
  ]);

  const onApproveTrustedSellereAlert = useCallback(() => {
    appLogger.info(
      "Update-Item-Quantity: Approved for editing trusted seller quantity",
      {}
    );
    setTrustedSellerWarning(false);
    onClickReceive();
  }, [onClickReceive, setTrustedSellerWarning]);

  const onApproveRegulatedItemLables = useCallback(() => {
    appLogger.info(
      "Item-Quantity: approved all regulated items are labelled",
      {}
    );
    setopenRegulatedModal(false);
    receive();
  }, [receive, setopenRegulatedModal]);

  const handleCloseOveragePopup = () => {
    appLogger.action("item-quantity: navigate to scan-cart page");
    navigate("/");
  }

  const onClickOverageCheck = useCallback(async () => {
    const quantity =
      scanUpcNavState?.gdmDeliveryDocuments[0].deliveryDocumentLines[0]
        ?.openQty != null ||
      scanUpcNavState?.gdmDeliveryDocuments[0].deliveryDocumentLines[0]
        ?.openQty !== undefined
        ? scanUpcNavState?.gdmDeliveryDocuments[0].deliveryDocumentLines[0]
            ?.openQty
        : null;
    const enteredQty = parseInt(qty);

    const isprinterSelected = await printingHandler.isPrinterSelected();
    //this code is for each level adjustment testing.

    const trustedQuantity =
      scanUpcNavState && scanUpcNavState.itemQuantity
        ? parseInt(scanUpcNavState.itemQuantity)
        : null;

    //this code is for each level adjustment testing.
    if (
      quantity === null &&
      trustedQuantity !== null &&
      trustedQuantity !== enteredQty
    ) {
      if (isprinterSelected) {
        setHeader(<>Warning alert!</>);
        setContent(
          <>
            <p>
              You are trying to edit trusted seller case quantity. Please
              re-confirm or cancel to return.
            </p>
          </>
        );
        setTrustedSellerWarning(true);
      } else {
        setAlerts({
          error:
            "There was an error printing. Check the printer status or re-connect to continue.",
        });
      }
    } else if (
      quantity !== null &&
      trustedQuantity !== null &&
      enteredQty > quantity &&
      enteredQty !== trustedQuantity
    ) {
      if (isprinterSelected) {
        setHeader(<>Warning alert!</>);
        setContent(
          <>
            <p>
              You are trying to edit trusted seller case quantity and the
              quantity entered will result in overages. Please confirm or cancel
              to return.
            </p>
          </>
        );
        setTrustedSellerWarning(true);
      } else {
        setAlerts({
          error:
            "There was an error printing. Check the printer status or re-connect to continue.",
        });
      }
    } else if (
      quantity !== null &&
      trustedQuantity !== null &&
      enteredQty <= quantity &&
      enteredQty !== trustedQuantity
    ) {
      if (isprinterSelected) {
        setHeader(<>Warning alert!</>);
        setContent(
          <>
            <p>
              You are trying to edit trusted seller case quantity. Please
              re-confirm or cancel to return.
            </p>
          </>
        );
        setTrustedSellerWarning(true);
      } else {
        setAlerts({
          error:
            "There was an error printing. Check the printer status or re-connect to continue.",
        });
      }
    }
    else if (quantity !== null && enteredQty > quantity) {
      if (isprinterSelected) {
        setHeader(<>Overage Quantity</>);
        setContent(
          <>
            <p>
              The quantity entered will result in overages.
              <br />
              Please confirm or cancel to return.
            </p>
          </>
        );
        setOpenOvergaeAlert(true);
      } else {
        setAlerts({
          error:
            "There was an error printing. Check the printer status or re-connect to continue.",
        });
      }
    } else {
      onClickReceive();
    }
  }, [onClickReceive, setOpenOvergaeAlert, qty, scanUpcNavState, setAlerts]);

  const onApproveOverageAlert = useCallback(() => {
    appLogger.info("Item-Quantity: approved for quantity overage", {});
    setOpenOvergaeAlert(false);
    onClickReceive();
  }, [onClickReceive, setOpenOvergaeAlert]);

  // disable global keyboard
  useGlobalKeyboard({
    disabled: true,
  });

  const onClickReceivingLargeQuantity = useCallback(() => {
    const enteredQty = parseInt(qty);
    const itemNbr = scanUpcNavState
      ? scanUpcNavState.gdmDeliveryDocuments[0]?.deliveryDocumentLines[0]
          .itemNbr
      : 0;
    const poNumber = scanUpcNavState
      ? scanUpcNavState.gdmDeliveryDocuments[0].purchaseReferenceNumber
      : 0;
    // check receiving soft limit
    if (enableReceivingLargeQuantityLimit && enteredQty > RCV_SOFT_LIMIT) {
      correlationId = uuid();
      appLogger.info(
        `Large Quantity Receiving: Soft Limit Alert - Receiving ${enteredQty} unit of item ${itemNbr} of PO: ${poNumber} excess limit ${RCV_SOFT_LIMIT}, correlationId ${correlationId}`,
        {}
      );
      setHeader(<>Receiving Large Quantity Alert!</>);
      setContent(
        <>
          <p>
            Please validate that you are receiving {enteredQty} units of item{" "}
            {itemNbr}
          </p>
        </>
      );
      setReceivingLargeQuantityDlg(true);
    } else {
      onClickOverageCheck();
    }
  }, [onClickOverageCheck, qty, scanUpcNavState]);

  const onConfirmReceivingLargeQuantityAlert = useCallback(() => {
    appLogger.info(
      `Item-Quantity: confirmed for large quantity, correlationId ${correlationId}`,
      {}
    );
    setReceivingLargeQuantityDlg(false);
    onClickOverageCheck();
  }, [onClickOverageCheck, setReceivingLargeQuantityDlg]);

  const onDeclineReceivingLargeQuantityAlert = useCallback(() => {
    appLogger.info(
      `Item-Quantity: declined for large quantity, correlationId ${correlationId}`,
      {}
    );
    setReceivingLargeQuantityDlg(false);
  }, [onClickOverageCheck, setReceivingLargeQuantityDlg]);

  const itemNbr = scanUpcNavState
    ? scanUpcNavState.gdmDeliveryDocuments[0]?.deliveryDocumentLines[0].itemNbr
    : 0;

  const itemDescription1 = scanUpcNavState
    ? getItemDescription(scanUpcNavState.gdmDeliveryDocuments)
    : "";

  const itemUPC = scanUpcNavState
    ? getUpc(scanUpcNavState.gdmDeliveryDocuments)
    : "";

  const imageUrl =
    scanUpcNavState?.gdmDeliveryDocuments[0]?.deliveryDocumentLines[0]
      .additionalInfo.imageUrl || "";

  return (
    <>
      <SubHeader
        title={`UPC: ${itemUPC} Count and enter qty`}
        enableBack
        chip={chipContent}
        onClickBack={onClickBack}
        actions={[
          <Button
            variant="outlined"
            key="cancel"
            size="large"
            onClick={onClickCancel}
            sx={{
              ...styles.button,
              color: "primary.dark",
              borderColor: "primary.dark",
            }}
          >
            Cancel
          </Button>,
          <Button
            variant="contained"
            key="receive"
            size="large"
            sx={styles.button}
            disabled={!parseInt(qty) || alerts.loading}
            onClick={onClickReceivingLargeQuantity}
          >
            Receive
          </Button>,
        ]}
      />
      <ItemSummary
        imageUrl={imageUrl}
        itemNbr={itemNbr}
        itemUPC={itemUPC}
        itemDescription={itemDescription1}
        value={qty}
        setValue={setQty}
        inputLabel="Enter Quantity"
      />
      {showHeavyLiftPopUp && printJobData && trackingId && containerWeight && (
        <DialogHeavyLift 
          open={true}
          header="Heavy Lift Label"
          content={`We've identified this Box is heavy! (${containerWeight} Lbs). Please place a Heavy label and carefully handle this case.\n\n
          Esta caja es pesada! (${containerWeight} Lbs). Coloque una pagetina de 'Heavy' y manipule con cuidado esta caja.\n\n
          Cette caisse est lourde! (${containerWeight} Lbs). Ajoutez une etiquette 'Heavy' et faites attention quand vous manipulez cette caisse.`}
          handleConfirm={()=>{
            const logInfo: HeavyLiftLabel = {
              trackingId: trackingId,
              correlationId: heavyLiftCorrelationId
            };
            appLogger.info('Heavy Lift Label Popup: Confirm', logInfo)
            setShowHeavyLiftPopUp(false);
            setPrintJobData(null);
            setTrackingId(undefined);
            printInstructionPrintJob(printJobData);
          }}
        />
      )}
      {showHardOveragePopUp && (
        <DialogOverage 
          handleClose={handleCloseOveragePopup} 
          header="Overage"
          content={"This receipt exceeds the allowable overage. Please contact your manager to open a ticket"}
          open={showHardOveragePopUp}
        />
      )}
      {openRegulatedModal && (
        <RegulatedItemModal
          regulatedItemDeliveryDocLine={
            scanUpcNavState?.gdmDeliveryDocuments[0].deliveryDocumentLines[0]
          }
          open={openRegulatedModal}
          onApprove={onApproveRegulatedItemLables}
          onClose={() => {
            setopenRegulatedModal(false);
          }}
        />
      )}
      {trustedSellerWarning && (
        <CustomDialogBox
          getHeader={header}
          getContent={content}
          handleConfirm={onApproveTrustedSellereAlert}
          handleClose={() => {
            setTrustedSellerWarning(false);
          }}
          open={trustedSellerWarning}
        />
      )}
      {openOverageAlert && (
        <CustomDialogBox
          getHeader={header}
          getContent={content}
          handleConfirm={onApproveOverageAlert}
          handleClose={() => {
            setOpenOvergaeAlert(false);
          }}
          open={openOverageAlert}
        />
      )}
      {receivingLargeQuantityDlg && (
        <CustomDialogBox
          getHeader={header}
          getContent={content}
          handleConfirm={onConfirmReceivingLargeQuantityAlert}
          handleClose={onDeclineReceivingLargeQuantityAlert}
          open={receivingLargeQuantityDlg}
        />
      )}
    </>
  );
}

const addGtinToDocumentLine = (deliveryDocs: DeliveryDocument[]) => {
  const deliveryDocsCopy: DeliveryDocument[] = JSON.parse(
    JSON.stringify(deliveryDocs)
  );
  const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
  const enableRegulatedItemChange =
    myAppsData?.dynamicConfig.enableRegulatedItemChange || false;
  deliveryDocsCopy.forEach((deliveryDoc) => {
    deliveryDoc.deliveryDocumentLines.forEach((docLine) => {
      docLine.gtin = enableRegulatedItemChange
        ? docLine.itemUpc
        : docLine.itemUPC;
    });
  });
  return deliveryDocsCopy;
};
