import { DeliveryDocument } from "../types/instruction";
import { MyAppsData } from "../types/my-apps";
import { sessionHandler } from "./session-handler";

const myAppsData = sessionHandler.getSessionStorage<MyAppsData>("myApps");
const enableRegulatedItemChange =
  myAppsData?.dynamicConfig.enableRegulatedItemChange || false;

export const getUpc = (item: DeliveryDocument[]): string => {
  const upcViaReceiving = item[0]?.deliveryDocumentLines[0].itemUpc || "";
  const upcViaGdm = item[0]?.deliveryDocumentLines[0].itemUPC || "";
  return enableRegulatedItemChange ? upcViaReceiving : upcViaGdm;
};

export const getItemDescription = (item: DeliveryDocument[]): string => {
  const itemDescViaReceiving =
    item[0]?.deliveryDocumentLines[0].description || "";
  const itemDescViaGdm =
    item[0]?.deliveryDocumentLines[0].itemDescription1 || "";
  return enableRegulatedItemChange ? itemDescViaReceiving : itemDescViaGdm;
};

export const getTotalOrderQuantity = (item: DeliveryDocument): number => {
  const totalOrderQuantityViaGdm = item.deliveryDocumentLines.reduce(
    (p, c) => p + c.expectedQty,
    0
  );
  const totalOrderQuantityViaReceiving = item.deliveryDocumentLines.reduce(
    (p, c) => p + (c.totalOrderQty || 0),
    0
  );

  return enableRegulatedItemChange
    ? totalOrderQuantityViaReceiving
    : totalOrderQuantityViaGdm;
};
