import React from 'react'
import { Dialog, Typography, Box, DialogContent, DialogContentText, Divider, IconButton, DialogActions, Button } from '@mui/material'
import CloseIcon from "@mui/icons-material/Close";
import { styles } from "./dialog-box.style";


interface overageProps {
    open: boolean,
    header: string,
    content: string,
    handleClose: () => void
}

export default function DialogOverage({ open, header, content, handleClose }: overageProps) {

  return (
    <Dialog 
        open={open}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        PaperProps={{ sx: styles.paper }}
        onClose={handleClose}
    >
        <Box sx={styles.titleContainer}>
            <Typography gutterBottom variant="h4" component="div">
              <b>{header}</b>
            </Typography>
            <IconButton aria-label="close" onClick={handleClose}>
              <CloseIcon fontSize="large" />
            </IconButton>
        </Box>

        <Divider />

        <DialogContent>
            <DialogContentText
              id="alert-dialog-description"
              sx={{ fontSize: "20px", color: "black" }}
            >
              {content.split('\n').map((line: String) => {
                return (
                  <div style={{"marginBottom": "15px"}}>
                    {line}
                  </div>
                )
              })}
            </DialogContentText>
        </DialogContent>
        <Divider />
          <DialogActions sx={{ m: 2 }}>
            <Button
              variant="contained"
              key="receive"
              size="large"
              sx={styles.button}
              onClick={handleClose}
            >
              Close
            </Button>
        </DialogActions>
    </Dialog>
  )
}
